@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@100&display=swap')



* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'IBM Plex Mono';
}

/* body {
	text-align: center;
	background-image: linear-gradient(to left, #545352 0%, #304352 100%);
} */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.LinkA,
.LinkSpan {
	color: #fbfbff;
	position: relative;
	color: inherit;
	text-decoration: none;
	font-family: 'IBM Plex Mono';
	font-weight: 900;
}
.LinkA:before,
.LinkA:after,
.LinkSpan:before,
.LinkSpan:after {
	content: '';
	position: absolute;
	transition: transform 0.3s ease;
}
.hover-1 {
	padding-top: 10px;
}
.hover-1:before {
	left: 0;
	bottom: 0;
	width: 100%;
	height: 2px;
	background: #492059;
	transform: scaleX(0);
}
.hover-1:hover:before {
	transform: scaleX(1);
}
.hover-2 {
	padding: 10px;
}
.hover-2:before,
.hover-2:after {
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	border-style: solid;
	border-color: #492059;
}
.hover-2:before {
	border-width: 2px 0 2px 0;
	transform: scaleX(0);
}
.hover-2:after {
	border-width: 0 2px 0 2px;
	transform: scaleY(0);
}
.hover-2:hover:before,
.hover-2:hover:after {
	transform: scale(1, 1);
}
.hover-3 {
	display: inline-flex;
	padding-top: 10px;
	padding-bottom: 10px;
	overflow: hidden;
}
.hover-3:before {
	left: 0;
	bottom: 0;
	width: 100%;
	height: 1px;
	background: #b5fed9;
	transform: translateX(-100%);
}
.hover-3:hover:before {
	transform: translateX(0);
}
.hover-4 {
	padding: 10px;
	display: inline-flex;
	overflow: hidden;
}
.hover-4:before,
.hover-4:after {
	left: 0;
	width: 100%;
	height: 2px;
	background: #492059;
}
.hover-4:before {
	bottom: 0;
	transform: translateX(-100%);
}
.hover-4:after {
	top: 0;
	transform: translateX(100%);
}
.hover-4:hover:before,
.hover-4:hover:after {
	transform: translateX(0);
}
.hover-5 {
	display: inline-block;
	overflow: hidden;
}
.hover-5:before,
.hover-5:after {
	right: 0;
	bottom: 0;
	background: #492059;
}
.hover-5:before {
	width: 100%;
	height: 2px;
	transform: translateX(-100%);
}
.hover-5:after {
	width: 2px;
	height: 100%;
	transform: translateY(100%);
}
.hover-5 > span {
	display: flex;
	padding: 10px;
}
.hover-5 > span:before,
.hover-5 > span:after {
	left: 0;
	top: 0;
	background: #492059;
}
.hover-5 > span:before {
	width: 100%;
	height: 2px;
	transform: translateX(100%);
}
.hover-5 > span:after {
	width: 2px;
	height: 100%;
	transform: translateY(-100%);
}
.hover-5:hover:before,
.hover-5:hover:after,
.hover-5:hover > span:before,
.hover-5:hover > span:after {
	transform: translate(0, 0);
}
.hover-6 {
	display: inline-flex;
	overflow: hidden;
}
.hover-6:before,
.hover-6:after {
	right: 0;
	bottom: 0;
	background: #492059;
	transition: transform 0.3s ease;
}
.hover-6:before {
	width: 100%;
	height: 2px;
	transform: translateX(-100%);
	transition-delay: 0.9s;
}
.hover-6:after {
	width: 2px;
	height: 100%;
	transform: translateY(100%);
	transition-delay: 0.6s;
}
.hover-6 > span {
	display: flex;
	padding: 10px;
}
.hover-6 > span:before,
.hover-6 > span:after {
	left: 0;
	top: 0;
	background: #492059;
	transition: transform 0.3s ease;
}
.hover-6 > span:before {
	width: 100%;
	height: 2px;
	transform: translateX(100%);
	transition-delay: 0.3s;
}
.hover-6 > span:after {
	width: 2px;
	height: 100%;
	transform: translateY(-100%);
	transition-delay: 0s;
}
.hover-6:hover:before,
.hover-6:hover:after,
.hover-6:hover > span:before,
.hover-6:hover > span:after {
	transform: translate(0, 0);
}
.hover-6:hover:before {
	transition-delay: 0s;
}
.hover-6:hover:after {
	transition-delay: 0.3s;
}
.hover-6:hover > span:before {
	transition-delay: 0.6s;
}
.hover-6:hover > span:after {
	transition-delay: 0.9s;
}
.hover-7 {
	display: inline-block;
	overflow: hidden;
}
.hover-7:before {
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: #492059;
	transform: translateX(-100%);
}
.hover-7:after {
	content: attr(data-content);
	left: 0;
	top: 0;
	width: 0;
	margin: 12px;
	color: #492059;
	white-space: nowrap;
	overflow: hidden;
	transition: width 0.5s ease;
}
.hover-7 > span {
	display: block;
	margin: 2px;
	padding: 10px;
	background: #9556ae;
}
.hover-7:hover:before {
	transform: translateX(0);
}
.hover-7:hover:after {
	width: calc(100% - 24px);
}
.hover-8 {
	display: inline-flex;
	overflow: hidden;
}
.hover-8:before {
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: #492059;
	transform: translateY(-100%);
}
.hover-8:after {
	content: attr(data-content);
	top: 0;
	left: 0;
	height: 0;
	margin: 12px;
	color: #492059;
	white-space: nowrap;
	overflow: hidden;
	transition: height 0.5s ease;
}
.hover-8 > span {
	display: block;
	margin: 2px;
	padding: 10px;
	background: #9556ae;
}
.hover-8:hover:before {
	transform: translateY(0);
}
.hover-8:hover:after {
	height: calc(100% - 24px);
}
.footer {
	text-align: center;
}
.footer a {
	color: #492059;
}

.UL {
	display: flex;
	position: absolute;
	margin: 0;
	padding: 0;
	transform: translate(-50%, -50%);
}

LI {
	list-style: none;
}

.ATAG {
	width: 40px;
	height: 40px;
	text-align: center;
	line-height: 40px;
	font-size: 15px;
	margin: 0 20px;
	display: block;
	border-radius: 100%;
	position: relative;
	overflow: hidden;
	border: 1px solid #fff;
	z-index: 1;
}

.icon {
	position: relative;
	color: white;
	transition: 0.5s;
	z-index: 3;
}

.ATAG:hover .icon {
	color: #fff;
	transform: rotateY(360deg);
}

.ATAG:before {
	content: '';
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;

	transition: 0.5s;
	z-index: 2;
}

.UL .LI .ATAG:hover:before {
	top: 0;
}

.FB:before {
	background: #3b5999;
}

.GH:before {
	background:#44CFCB;
}

.LK:before {
	background: #0077b5;
}

@media screen and (max-width: 768px) {
	.icon {
		height: 30px;
		width: 30px;
	}
}
@media screen and (max-width: 480px) {
	.icon {
		height: 15px;
		width: 15px;
	}
}

/*///////////////////////////////////////////////////
      // Skill Bars \\ 
  ///////////////////////////////////////////////////*/

.contentContainer {
	max-width: 555px;
	padding: 10px;
	max-width: 350px;
	min-width: 150px;
	margin: auto;
	border-radius: 10px;
	margin-bottom: 5%;
}
.skillBar {
	margin-bottom: 1%;
}
h4 {
	color: #fff;
	font-size: 21px;
	font-size: 1.33em;
	text-transform: none;
	font-family: Arial, Helvetica, sans-serif;
	font-weight: bold;
	margin-bottom: 7px;
	margin-bottom: 0.33em;
}
.skillBarContainer {
	width: 100%;
	max-width: 350px;
	height: 26px;
	height: 1.66em;
	background: #e6eae3;
	background: rgb(8, 102, 220);
	background: rgba(8, 102, 220, 0.2);
	overflow: hidden;
	border-radius: 5px;
}
.skillBarValue {
	height: 1.66em;
	float: left;
	background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}

/* Allowed values for the css skill bars */
.value-00 {
	width: 0;
}
.value-10 {
	width: 10%;
}
.value-20 {
	width: 20%;
}
.value-30 {
	width: 30%;
}
.value-40 {
	width: 40%;
}
.value-50 {
	width: 50%;
}
.value-60 {
	width: 60%;
}
.value-70 {
	width: 70%;
}
.value-80 {
	width: 80%;
}
.value-85 {
	width: 85%;
}
.value-90 {
	width: 90%;
}
.value-95 {
	width: 95%;
}
.value-100 {
	width: 100%;
}

/*///////////////////////////////////////////////////
      // Animation \\ 
  ///////////////////////////////////////////////////*/
@keyframes slideIn {
	0% {
		width: 0;
	}
	25% {
		width: 100%;
	}
	100% {
		width: normal;
	}
}
.skillBarValue {
	animation: slideIn 2s;
}

